.flickity-prev-next-button {
    width: 14px;
    height: 36px;
    background: transparent;

    &, &:focus, &:hover, &:active {
      outline: none;
      border: none;
      box-shadow: none;
    }
    
    .flickity-button-icon {
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
    }
  }

  .flickity-prev-next-button:hover {
    background: transparent;
  }
  /* arrow color */
  .flickity-prev-next-button .arrow {
    fill: #fff;
  }
  .flickity-prev-next-button.no-svg {
    color: #fff;
  }