.category-navigation {
    @include row;

    width: 72.054%;
    flex-basis: 72.054%;

    &__item {
        position: relative;
        padding-bottom: 3px;
        color: #4f4f4f;
        font-weight: 700;

        &_active {
            color: #7e9d51;
        }

        &:hover {
            &::after {
                content: '';
                position: absolute;
                right: 0;
                left: 0;
                bottom: 0;
                border-bottom: 2px solid #8fb35b;
            }
        }
    }
}